let utils = utils || {
  hasClass(el, cl) {
    const regex = new RegExp('(?:\\s|^)' + cl + '(?:\\s|$)');
    return !!el.className.match(regex);
  },

  addClass(el, cl) {
    el.className += ` ${cl}`;
  },

  removeClass(el, cl) {
    const regex = new RegExp('(?:\\s|^)' + cl + '(?:\\s|$)');
    el.className = el.className.replace(regex, '');
  },

  toggleClass(el, cl) {
    const testClass = this.hasClass(el, cl) ? this.removeClass(el, cl) : this.addClass(el, cl);
  },

  getNextElementSibling(el) {
    if (el.nextElementSibling) {
      return el.nextElementSibling;
    } else {
      do {
        el = el.nextSibling;
      }
      while (el && el.nodeType !== 1);
      return el;
    }
  },

  getPreviousElementSibling(el) {
    if (el.PreviousElementSibling) {
      return el.PreviousElementSibling;
    } else {
      do {
        el = el.previousSibling;
      }
      while (el && el.nodeType !== 1);
      return el;
    }
  },

  elementInViewport(el) {
    let r;
    let html;
    if (!el || 1 !== el.nodeType) {
      return false;
    }
    html = document.documentElement;
    r = el.getBoundingClientRect();
    return (!!r && r.bottom >= 0 && r.right >= 0 && r.top <= html.clientHeight && r.left <= html.clientWidth);
  },

  sameHeight(el) {
    let maxHeight = 0;
    for (let i = 0; i < el.length; i++) {
      let thisHeight = el[i].offsetHeight;
      if (thisHeight > maxHeight) {
        maxHeight = thisHeight;
      }
    }
    for (let i = 0; i < el.length; i++) {
      el[i].style.height = maxHeight + 'px';
    }
  },

  distanceFromTop(el) {
    const rect = el.getBoundingClientRect();
    const docEl = document.documentElement;
    return {
        top: rect.top + (window.pageYOffset || docEl.scrollTop || 0)
    };
  },

  removeElement(el) {
    el.parentNode.removeChild(el);
  },

  debounceEvent(event) {
    let scheduledAnimationFrame = false;
    if (scheduledAnimationFrame) {
      return;
    }
    scheduledAnimationFrame = true;
    requestAnimationFrame ? requestAnimationFrame(event) : event();
  }
};

export default utils;
