import utils from '../base/utils';

let lazyload = lazyload || (() => {
  return {
    init() {
      var layzr = new Layzr({
        threshold: 50,
        callback: function() {
          this.addEventListener('load', () => {
            utils.addClass(this.parentNode, 'is-loaded');
            utils.addClass(this, 'is-loaded');
          });
        }
      });
    }
  };
})();

lazyload.init();

export default lazyload;
