import utils from '../base/utils';

let canvas = canvas || (() => {
  const particles = document.getElementById('particles-js');

  return {
    init() {
      if (particles) {
        particlesJS.load('particles-js', '/assets/json/particles.json', () => {
          window.setTimeout(() => {
            utils.addClass(particles, 'is-visible');
          }, 300);
        });
      }
    }
  };
})();

canvas.init();
