import utils from '../base/utils';
import lazyload from '../modules/lazyload';

let navigation = navigation || (() => {
  const navToPages = document.querySelectorAll('.js-nav-to-pages');
  const intro = document.getElementById('js-intro');
  const contentPlaceholder = document.querySelector('.js-content-placeholder');

  return {
    init() {
      this.binding();
    },

    binding() {
      for (let i = 0; i < navToPages.length; i ++) {
        navToPages[i].addEventListener('click', e => {
          if (history.pushState) {
            e.preventDefault();
            utils.addClass(intro, 'is-hidden');
            window.setTimeout(() => {
              navigation.AJAXNav(navToPages[i].getAttribute('href'));
            }, 1000);
          }
        });
      }

      window.addEventListener('load', () => {
        window.setTimeout(() => {
          window.addEventListener('popstate', () => {
            window.location.reload();
          });
        }, 0);
      });
    },

    AJAXNav(url) {
      const request = new XMLHttpRequest();
      request.open('GET', url, true);

      request.onload = () => {
        if (request.status >= 200 && request.status < 400) {
          const newEl = document.createElement('div');
          document.body.appendChild(newEl);
          contentPlaceholder.innerHTML = request.responseText;
          newEl.innerHTML = contentPlaceholder.querySelector('.js-content').innerHTML;
          document.title = contentPlaceholder.querySelector('title').innerHTML;
          history.pushState({}, '', url);
          console.log('f');
          utils.removeElement(intro);
          window.setTimeout(() => {
          utils.addClass(document.querySelector('.wrapper'), 'is-visible');

        }, 0);
          lazyload.init();
        }
        else {
          alert(request.responseText);
          window.location.reload();
        }
      };

      request.onerror = () => {
        alert(request.responseText);
        window.location.reload();
      };

      request.send();
    }
  };
})();

navigation.init();
